.popup {
  display: none;
  position: fixed;
  width: 90%;
  left: 50%;
  top: 1%;
  transform: translate(-50%, -50%);
  z-index: 5;
}

.popup--show {
  display: block;
}

.popup__wrapper {
  position: absolute;
  width: 100%;
  padding: 20px 50px 5px;
  background-color: white;
  box-shadow: 0 5px 20px 0 #32CD52;
  box-sizing: border-box;
}

.popup__title {
  margin-top: 14px;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
}

.popup__title--time {
  color: #dc143c;
  margin-bottom: 30px;
}

.popup__table {
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 30px;
}

.popup__table-tag {
  max-width: 600px;
  margin: 0 auto;
  border-collapse: collapse;
}

.popup__cell {
	border: 1px solid #000;
	width: 100px;
  min-width: 40px;
  max-width: 300px;
  overflow: hidden;
	padding: 3px 10px;
  text-overflow: ellipsis;
  box-sizing: border-box;
}

.popup__text {
  font-size: 30px;
  text-align: center;
  line-height: 24px;
  margin: 20px 0 40px;
}

.popup__btn {
  display: block;
  width: 250px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 15px 40px;
  text-align: center;
  border: none;
  font-size: 24px;
  font-weight: 700;
  background-color: #DC143C;
  color: white;
  text-decoration: none;
  cursor: pointer;
  overflow: hidden;
  filter: opacity(70%);
}

.popup__btn:hover,
.popup__btn--game:hover {
  filter: opacity(85%);
}

.popup__btn:active,
.popup__btn--game:active {
  filter: opacity(100%);
}

.popup__btn--game {
  background-color: #32CD52;
}

.popup__btn--sendScore {
  padding: 10px 30px;
  background-color: #429ed4;
}
